import axios from "axios";
import DropdownCheckbox from "./DropdownCheckbox";
import {
  Button,
  Checkbox,
  Divider,
  List,
  ListItem,
  ListItemText,
  TextField,
  FormGroup,
  FormControlLabel,
} from "@mui/material";
import React, { useState } from "react";
import "./Search.css";

export default function Search() {
  const [query, setQuery] = useState("");
  const [wantedTitles, setWantedTitles] = useState([]);
  const [works, setWorks] = useState([]);
  const [exactMatch, setExactMatch] = useState(true);

  function handleSelectedTitles(selected) {
    console.log(selected);
    setWantedTitles(selected);
  }

  function handleSubmit(e) {
    e.preventDefault();
    const params = "?" + "q=" + query + "&" + "exactMatch=" + exactMatch;
    const url = "https://kw-shakesearch-api.onrender.com/search" + params;
    const localHost = "http://localhost:3001/search" + params;
    let selectedTitles = [];
    for (const titleObject of wantedTitles) {
      selectedTitles.push(titleObject.value);
    }
    console.log(selectedTitles);
    var config = {
      method: "post",
      url: url,
      headers: {
        "Content-Type": "text/plain",
      },
      data: selectedTitles,
    };

    axios(config)
      .then(function (response) {
        let data = response.data;
        setWorks(data);
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  return (
    <div>
      <div className="search-wrapper">
        <div>
          <FormGroup>
            <FormControlLabel
              control={
                <Checkbox
                  onClick={() => setExactMatch(!exactMatch)}
                  defaultChecked
                />
              }
              label="*Exact match (Case insensitive)"
            />
          </FormGroup>
        </div>
        <form noValidate className="searchbar-button">
          <div className="searchbar">
            <TextField
              aria-required
              label="Try Hamlet"
              variant="outlined"
              fullWidth
              onChange={(e) => setQuery(e.target.value.toLowerCase())}
            />
          </div>
          <div className="button">
            <Button
              onClick={handleSubmit}
              variant="contained"
              sx={{
                minHeight: "100%",
                maxHeight: "100%"
              }}
            />
          </div>
        </form>
        <div className="title-dropdown">
          <DropdownCheckbox handleSelectedTitles={handleSelectedTitles} />
        </div>
      </div>
      {<RenderList works={works} query={query} exactMatch={exactMatch} />}
    </div>
  );
}

const RenderList = (props) => {
  if (props.works == null || props.works.length === 0) {
    return null;
  }
  return (
    <div>
      <h3 style={{ textAlign: "center" }}>
        {props.exactMatch
          ? "Results (" + props.works.length + "): *" + props.query
          : "Results (" + props.works.length + "): " + props.query}
      </h3>
      <List sx={{ width: "100%", bgcolor: "background.paper" }}>
        {props.works.map((item, i) => {
          return (
            <div key={i}>
              <ListItem alignItems="flex-start">
                <ListItemText
                  primary={item.Title}
                  secondary={
                    <FormatSnippet Query={props.query} Snippet={item.Snippet} />
                  }
                />
              </ListItem>
              <Divider variant="inset" component="li" />
            </div>
          );
        })}
      </List>
    </div>
  );
};

const FormatSnippet = (props) => {
  const fullText = props.Snippet;
  const query = props.Query;
  var re = new RegExp(query, "gi");
  const formattedSnippet = fullText.split(/\r/).map((line, i) => {
    var matches = [];
    var match;
    while ((match = re.exec(line))) matches.push(match.index);

    console.log(line);
    console.log(matches);
    if (matches.length != 0) {
      var shift = 0;
      const formattedLine = matches.map((foundIndex) => {
        foundIndex = foundIndex - shift;
        var nonHighlighted = line.substring(0, foundIndex);
        var highlighted = line.substring(foundIndex, foundIndex + query.length);
        shift = nonHighlighted.length + highlighted.length;
        line = line.substring(foundIndex + query.length, line.length);
        return (
          <a key={foundIndex}>
            {nonHighlighted}
            <mark>{highlighted}</mark>
          </a>
        );
      });
      return (
        <p key={i}>
          {formattedLine}
          {line}
        </p>
      );
    } else {
      return <p key={i}>{line}</p>;
    }
  });
  return formattedSnippet;
};
